import { Input, Typography } from "antd";
import { useEffect, useState } from "react";
import Tesseract from "tesseract.js";
import UICCheckNumber from "../../utils/UICCheckNumber";
const TextRecognition = ({
  selectedImage,
  changeValue,
}: {
  selectedImage: any;
  changeValue: any;
}) => {
  const [recognizedText, setRecognizedText] = useState("");

  const handlerChangeValue = (value: string) => {
    changeValue(value);
    setRecognizedText(value);
  };

  useEffect(() => {
    const recognizeText = async () => {
      if (selectedImage) {
        const worker = await Tesseract.createWorker();
        worker.setParameters({
          tessedit_char_whitelist: "0123456789",
        });
        const result = await worker.recognize(selectedImage);
        handlerChangeValue(result.data.text);
      }
    };
    recognizeText();
  }, [selectedImage]);

  const good = UICCheckNumber(recognizedText);
  return (
    <div>
      <Typography.Title level={5}>UIC Number</Typography.Title>
      <Input
        value={recognizedText}
        count={{ show: true, max: 12 }}
        variant="filled"
        onChange={(e) => handlerChangeValue(e.currentTarget.value)}
        status={good ? "" : "error"}
      />
    </div>
  );
};
export default TextRecognition;
