import { PlusCircleOutlined } from "@ant-design/icons";
import { Button, Flex, Input } from "antd";
import { useState } from "react";
import ImageUploader from "../ImageUploader/ImageUploader";
import TextRecognition from "../TextRecognition/TextRecognition";

export interface UICFromImageProps {
  changeUicNumber: (uicNumber: string) => void;
}

const UICFromImage = (props: UICFromImageProps) => {
  const [currentImage, setCurrentImage] = useState<string | null>(null);

  return (
    <Flex vertical>
      <ImageUploader
        selectedImage={currentImage}
        setSelectedImage={setCurrentImage}
      />
      <TextRecognition
        selectedImage={currentImage}
        changeValue={props.changeUicNumber}
      />
    </Flex>
  );
};

export default UICFromImage;
