import { makeAutoObservable } from "mobx";

class RollingStockData {
  private id: number;
  private uicNumber: string;
  private type: string;
  private mass: number = 0;
  private length: number = 0;

  constructor(id: number, uicNumber: string, type: string) {
    makeAutoObservable(this);
    this.id = id;
    this.uicNumber = uicNumber;
    this.type = type;
  }

  toJson() {
    return {
      id: this.id,
      uicNumber: this.uicNumber,
      mass: this.mass,
      length: this.length,
    };
  }

  setUicNumber(uicNumber: string) {
    this.uicNumber = uicNumber;
  }

  setMass(mass: number) {
    this.mass = mass;
  }

  setLength(length: number) {
    this.length = length;
  }

  getMass() {
    return this.mass;
  }

  getLength() {
    return this.length;
  }

  getUicNumber() {
    return this.uicNumber;
  }

  getImageUrl() {
    return require("../images/rollingstock/wagon.png");
  }
}

export default RollingStockData;
