import {
  Flex,
  Typography,
  Card,
  Button,
  Descriptions,
  Divider,
  Layout,
  Input,
} from "antd";
import TrainData from "../../data/TrainData";
import RollingStock from "../RollingStock/RollingStock";
import RollingStockData from "../../data/RollingStockData";
import { EditOutlined, PlusCircleOutlined } from "@ant-design/icons";
import { observer } from "mobx-react-lite";
import { useState } from "react";
import TrainIdentification from "../TrainIdentification/TrainIdentification";
export interface TrainProps {
  trainData: TrainData;
  deleteTrain: () => void;
}

const Train = observer((props: TrainProps) => {
  const trainMass = props.trainData
    .getRollingStocks()
    .map((stock) => {
      return stock.getMass();
    })
    .reduce((acc, mass) => {
      return acc + mass;
    }, 0);

  const trainLength = props.trainData
    .getRollingStocks()
    .map((stock) => {
      return stock.getLength();
    })
    .reduce((acc, length) => {
      return acc + length;
    }, 0);

  const itemsCompo = [
    {
      key: "2",
      label: "Length",
      children: `${trainLength}m`,
    },
    {
      key: "3",
      label: "Mass",
      children: `${trainMass}t`,
    },
    {
      key: "4",
      label: "Vehicules",
      children: props.trainData.getRollingStocks().length,
    },
  ];

  const handleExportJson = () => {
    const trainJson = props.trainData.toJson();
    const jsonBlob = new Blob([JSON.stringify(trainJson)], {
      type: "application/json",
    });
    const downloadLink = document.createElement("a");
    downloadLink.href = URL.createObjectURL(jsonBlob);
    downloadLink.download = `${props.trainData.getName()}.json`;
    downloadLink.click();
  };

  return (
    <Card
      extra={
        <Flex gap={5}>
          <Button
            type="text"
            icon={<PlusCircleOutlined />}
            onClick={handleExportJson}
          >
            Export as JSON
          </Button>
          <Button type="text" danger onClick={props.deleteTrain}>
            Delete Train
          </Button>
        </Flex>
      }
    >
      <Flex gap={20}>
        <TrainIdentification trainData={props.trainData} />

        <Descriptions
          title="Composition"
          items={itemsCompo}
          column={1}
          bordered
          size="small"
        />
      </Flex>
      <Divider orientation="left">Composition detail</Divider>
      <Flex justify="left" gap={0} style={{ overflowX: "auto" }} align="center">
        {props.trainData.getRollingStocks().map((stock: any, index: number) => {
          return (
            <RollingStock
              trainPosition={index + 1}
              remove={() => props.trainData.removeRollingStock(index)}
              rollingStock={stock}
            />
          );
        })}
        <Button
          onClick={() =>
            props.trainData.addRollingStock(new RollingStockData(0, "", ""))
          }
          icon={<PlusCircleOutlined />}
          type="text"
          size="small"
        >
          Add Rolling Stock
        </Button>
      </Flex>
    </Card>
  );
});

export default Train;
