import RollingStockData from "./RollingStockData";
import { makeAutoObservable } from "mobx";

class TrainData {
  private id: number = 0;
  private name: string = "";
  private type: string = "";
  private circulationNumber: string = "";
  private rollingStocks: RollingStockData[] = [];

  constructor() {
    makeAutoObservable(this);
  }

  // Getter methods
  getId(): number {
    return this.id;
  }
  getName(): string {
    return this.name;
  }
  getType(): string {
    return this.type;
  }
  getCirculationNumber(): string {
    return this.circulationNumber;
  }
  getRollingStocks(): RollingStockData[] {
    return this.rollingStocks;
  }

  // Setter methods
  setId(id: number): void {
    this.id = id;
  }
  setName(name: string): void {
    this.name = name;
  }
  setType(type: string): void {
    this.type = type;
  }
  setCirculationNumber(circulationNumber: string): void {
    this.circulationNumber = circulationNumber;
  }
  setRollingStocks(rollingStocks: RollingStockData[]): void {
    this.rollingStocks = rollingStocks;
  }
  addRollingStock(rollingStock: RollingStockData): void {
    this.rollingStocks.push(rollingStock);
  }
  removeRollingStock(index: number): void {
    this.rollingStocks.splice(index, 1);
  }

  toJson() {
    return {
      id: this.id,
      name: this.name,
      type: this.type,
      rollingStocks: this.rollingStocks.map((stock) => stock.toJson()),
    };
  }
}

export default TrainData;
