import { useState } from "react";
import TrainData from "../../data/TrainData";
import { EditOutlined } from "@ant-design/icons";
import { Descriptions, Flex, Button, Input, Select } from "antd";
import { observer } from "mobx-react-lite";

export interface TrainIdentificationProps {
  trainData: TrainData;
}
const TrainIdentification = observer((props: TrainIdentificationProps) => {
  const trainTypeOptions: any = [
    { label: "Freight", value: "freight" },
    { label: "Passenger", value: "passenger" },
  ];

  const itemsIdentification = [
    {
      key: "1",
      label: "Name",
      children: (
        <Input
          type="text"
          onChange={(e) => {
            props.trainData.setName(e.currentTarget.value);
          }}
          value={props.trainData.getName()}
          size="small"
          variant="borderless"
        />
      ),
    },
    {
      key: "2",
      label: "Circulation Number",
      children: (
        <Input
          type="text"
          onChange={(e) => {
            props.trainData.setCirculationNumber(e.currentTarget.value);
          }}
          value={props.trainData.getCirculationNumber()}
          size="small"
          variant="borderless"
        />
      ),
    },
    {
      key: "3",
      label: "Type",
      children: (
        <Select
          value={props.trainData.getType()}
          options={trainTypeOptions}
          onChange={(value) => props.trainData.setType(value)}
          variant="borderless"
          size="small"
          style={{ width: "100%", textAlign: "left" }}
        />
      ),
    },
  ];

  return (
    <Descriptions
      title="Identification"
      items={itemsIdentification}
      column={1}
      bordered
      size="small"
    />
  );
});

export default TrainIdentification;
