import { DeleteOutlined, TagsOutlined } from "@ant-design/icons";
import {
  Card,
  Button,
  Tag,
  Input,
  Image,
  Flex,
  Dropdown,
  MenuProps,
  Modal,
  Descriptions,
} from "antd";
import UICCheckNumber from "../../utils/UICCheckNumber";
import RollingStockData from "../../data/RollingStockData";
import { observer } from "mobx-react-lite";
import { useState } from "react";
import UICFormat from "../../utils/UICFormat";
import { CopyToClipboard } from "react-copy-to-clipboard";
import UICFromImage from "../UICFromImage/UICFromImage";

function displayUICNumber(uicNumber: string) {
  if (UICCheckNumber(uicNumber)) {
    return uicNumber.replace(
      /(\d{2})(\d{2})(\d{4})(\d{3})(\d{1})/,
      "$1 $2 $3 $4-$5"
    );
  }
  return "Invalid UIC Number";
}

const RollingStock = observer(
  (props: {
    trainPosition: number;
    rollingStock: RollingStockData;
    remove: () => void;
  }) => {
    const { rollingStock } = props;

    const [loadUicNumber, setLoadUicNumber] = useState(false);
    const [hovered, setHovered] = useState(false);

    const isUicNumberValid = UICCheckNumber(rollingStock.getUicNumber());

    const uicNumberItems: MenuProps["items"] = [
      {
        key: "1",
        label: "Explain",
      },
      {
        key: "2",
        label: (
          <span onClick={() => setLoadUicNumber(true)}>Load from image</span>
        ),
      },
      {
        key: "3",
        label: (
          <CopyToClipboard
            text={UICFormat(rollingStock.getUicNumber())}
            onCopy={() => {
              console.log("copied");
            }}
          >
            <span>Copy to clipboard</span>
          </CopyToClipboard>
        ),
      },
    ];

    const rollingStockItems = [
      {
        key: "1",
        label: "Length",
        children: (
          <Input
            type="number"
            value={rollingStock.getLength()}
            onChange={(e) => {
              rollingStock.setLength(Number(e.target.value));
            }}
            placeholder="Length"
            min={0}
            variant="borderless"
            size="small"
          />
        ),
      },
      {
        key: "12",
        label: "Mass",
        children: (
          <Input
            type="number"
            value={rollingStock.getMass()}
            onChange={(e) => {
              rollingStock.setMass(Number(e.target.value));
            }}
            placeholder="Mass"
            min={0}
            variant="borderless"
            size="small"
          />
        ),
      },
    ];

    return (
      <Flex vertical align="center" gap={10} style={{ minWidth: 200 }}>
        <Modal
          title="Edit UIC Number"
          open={loadUicNumber}
          footer={null}
          onCancel={() => setLoadUicNumber(false)}
        >
          <UICFromImage
            changeUicNumber={(uicNumber) => {
              rollingStock.setUicNumber(uicNumber);
            }}
          />
        </Modal>
        <Image width={"100%"} src={rollingStock.getImageUrl()} />
        <Dropdown menu={{ items: uicNumberItems }} placement="bottom" arrow>
          <Tag
            icon={<TagsOutlined />}
            color={isUicNumberValid ? "blue" : "red"}
          >
            {displayUICNumber(rollingStock.getUicNumber())}
          </Tag>
        </Dropdown>
        {/* // extra={
          //   hovered && (
          //     <Button onClick={props.remove} icon={<DeleteOutlined />} />
          //   )
          // } */}

        {/* <Input
            type="text"
            value={rollingStock.getUicNumber()}
            onChange={(e) => rollingStock.setUicNumber(e.target.value)}
            status={isUicNumberValid ? "" : "error"}
          />
          <Input
            type="number"
            value={rollingStock.getMass()}
            onChange={(e) => {
              rollingStock.setMass(Number(e.target.value));
            }}
            placeholder="Mass"
            min={0}
          /> */}
        <Descriptions
          items={rollingStockItems}
          column={1}
          bordered
          size="small"
          style={{ padding: "0 10px" }}
        />
      </Flex>
    );
  }
);

export default RollingStock;
